.login {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: black;
}

.login img {
  width: 75%;
}

.login a {
  padding: 20px;
  background-color: #1db964;
  border-radius: 99px;
  font-weight: 800;
  color: white !important;
  text-decoration: none !important;
  cursor: pointer;
}
